.sofax-hero{
    &-section{
        padding-top: 190px;
        @include max(991px) {
            padding-top: 140px;
        }
        @include max(767px) {
            padding-top: 120px;
        }
        @include max(575px) {
            padding-top: 100px;
        }
    }
    
    &-content{
        
        span{
            margin-right: 12%;
        }
        &.center{
            text-align: center;
            max-width: 1074px;
            margin: 0 auto;
            margin-bottom: 50px;
            @include max(1199px) {
                max-width: 700px;
            }
            @include max(991px) {
                margin-bottom: 40px;
            }
            @include max(767px) {
                margin-bottom: 30px;
            }
            &.max-width-medium {
                text-align: center;
                max-width: 1100px;
                margin: 0 auto;
            }
            &.max-width-large {
                text-align: center;
                max-width: 1200px;
                margin: 0 auto;
            }
        }
        &.center-center{
            text-align: center;
            max-width: 1200px;
            margin: 0 auto;
            margin-bottom: 50px;
            @include max(991px) {
                margin-bottom: 40px;
            }
            @include max(767px) {
                margin-bottom: 30px;
            }
        }
        p{
            font-size: 20px;
        }
       
        
        @include max(991px) {
            text-align: center;
            @include max(1199px) {
                max-width: 650px;
                margin: 0 auto;
            }
        }
    }
}
.sofax-rating-icon  {
    text-align: center;
    padding-top: 15px;
    ul {
        li {
            display: inline-block;
            font-size: 18px;
            line-height: 28px;
            &:last-child{
                margin-left: 5px;
            }
        }
    }
}
//  end hero section v1

.sofax-app-wrap {
    display: flex;
    align-items: center;
    gap: 24px;
    flex-wrap: wrap;
    position: relative;
    @include max (991px){
        justify-content: center;
    }
}

@keyframes animName {
0%{
    transform: rotate(0deg);
    }
100%{
    transform: rotate(360deg);
    }
}
  
@keyframes float {
	0% {
		transform: translatex(0px);
	}
	50% {
		transform: translatex(-30px);
	}
	100% {
		transform: translatex(0px);
	}
}
@keyframes updown {
	0% {
		transform: translatey(0px);
	}
	50% {
		transform: translatey(-30px);
	}
	100% {
		transform: translatey(0px);
	}
}
@keyframes downup {
	0% {
		transform: translatey(0px);
	}
	50% {
		transform: translatey(30px);
	}
	100% {
		transform: translatey(0px);
	}
}
.pb-80{
    padding-bottom: 80px!important;
    @include max (991px){
        padding-bottom: 60px;
    }
    @include max (767px){
        padding-bottom: 40px;
    }
}
//  sofax hero thumb v3
.hero3-thumb{
    img{
        width: 100%;
    }
}
// hero thumb v6


.sofax_screenfix_right {
    margin-right: calc((-100vw + 100%)/ 2);
    max-width: 1610px;
    @include max(991px) {
        max-width: 100%;
        margin-right: auto;
    }
}
.hero-thumbv6 {
    margin-left: 80px;
    @include max(991px){
        margin-left: 0;
        margin-top: 40px;
    }
}
// v8
.sofax-hero-thumb8{
    text-align: center;
    @include max (991px){
        margin-top: 50px;
    }
    @include max (767px){
        margin-top: 30px;
    }
}
.hero-v8{
    h1{
        font-size: 100px!important;
        line-height: 110px;
        @include max (1199px){
            font-size: 75px !important;
            line-height: 85px;
        }
        @include max (991px){
            font-size: 85px !important;;
            line-height: 90px;
        }
        @include max (767px){
            font-size: 65px !important;
            line-height: 70px;
        }
        @include max (425px){
            font-size: 48px !important;
            line-height: 55px;
        }
    }
    p{
        font-size: 18px!important;
    }
}
// v9
.hero9-thumb{
    img {
        width: 100%!important;
    }
}
.hero-thumbv10{
    text-align: center;
}

.sofax-hero-content-v10{
    
    @include max (991px)
    {
        text-align: center;
        margin-bottom: 40px;
    }
    @include max (767px)
    {
        margin-bottom: 30px;
    }
}


.slider-custom-anim-left {
  opacity: 1;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-duration: 1s;
  animation-delay: 0.3s;
  animation-name: var(--animation-name);
}


.slider-custom-anim-left{
    --animation-name: custom-anim-left;
  }

.custom-anim-left {
    animation: custom-anim-left 1.3s forwards cubic-bezier(0.645, 0.045, 0.355, 1) 0.4s;
    opacity: 0;
  }
  
  @keyframes custom-anim-left {
    0% {
      transform: translateX(-5%);
      clip-path: inset(0 100% 0 0);
      opacity: 0;
    }
    100% {
      transform: translateX(0);
      clip-path: inset(0 0 0 0);
      opacity: 1;
    }
  }
