.sofax-accordion{
    &-item{
      padding: 23px 24px;
      position: relative;
      margin-bottom: 24px;
      border-radius: 10px;
      transition: all 0.4s;
      border: 1px solid var(--light-color);
      &.about-us-section {
        border: 1px solid var(--dark-bg);
      }
      
      &.open .sofax-active-icon{
        transform: rotate(180deg);
        
      }
      &.open{
        border: 1px solid #fff;
        box-shadow: 0px 4px 80px 0px #00000014;
      }
      &.open{
        .sofax-accordion-body{
          display: block;
         p{
            font-size: 20px!important;
         }
        }
      }
    }
    &-header{
      cursor: pointer;
      position: relative;
      display: flex;
      justify-content: space-between;
      h3{
        @include max(767px) {
          font-size: 24px;
          padding-right: 25px;
        }
      }
     
    }
    &-body{
      display: none;
      padding-top: 15px;
      &.default{
        display: block;
      }
      @include max(991px) {
        padding: 24px 0 0;
      }
    }
    // End one
    &-wrap2{
        .sofax-accordion-item.open {
            border: 1px solid var(--light-color2);
            box-shadow: none;
        }
    }
    &-wrap3{
      .sofax-accordion-item{
        &.open {
          border: 1px solid var(--dark-bg);
          background: var(--dark-bg);
          box-shadow: none;
          h4{
            color: var(--white-bg);
          }
          p{
            color: var(--white-bg);
          }
        }
      }
  }
}
  
.sofax-accordion-wrapper {
  display: flex;
  gap: 24px;
  @include max (991px){
      display: block;
  }
}
.sofax-active-icon{
  transition: all .4s;
  display: flex;
  width: 26px;
  flex-shrink: 0;
  height: 26px;
  padding: 5px;
  line-height: 31px;
  text-align: center;
  align-items: center;
  border-radius: 100px;
  justify-content: center;
  background-color: var(--accent-color);
    &.sofax-accordion-btn{
      img {
      width: 12px;
    }
  }
}
.sofax-accordion-section-wrapper{
    display: flex;
    gap: 24px;
    @include max (991px){
        display: block;
    }
}
.sofax-accordion-section-v2 {
    flex: 0 0 50%;
}
.accordion-button{
 color: var(--dark-bg)!important;
 background-color: var(body-color)!important;
 padding: 0;
}

.sofax-accordion-item.open {
  .version-6-faq{
    transform: none!important;
    background-color: var(--white-bg);
    img.plasicon{
      opacity: 0!important;
    }
    img.mynusicon{
      opacity: 1;
      margin-right: 22px;
      margin-top: 2px;
      display: block;
    }
  }
}

img.mynusicon {
  opacity: 0;
}
img.plasicon {
  opacity: 1;
  margin-left: 10px;
}


//  v7
.sofax-accordion{
  &-headerv7 {
    cursor: pointer;
    position: relative;
    display: flex;
    justify-content: space-between;
  }
}
.sofax-accordion-wrapperv7 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.sofax-accordion-item-v7.open .sofax-accordion-body-v7 {
  display: block;
}
.sofax-accordion-item.open {
  .version7{
    transform: rotate(180deg);
   
  }
}
.sofax-accordion-item-v7.open .sofax-active-icon-v7 {
  transform: rotate(180deg);
}
.sofax-active-icon-v7{
  background: var(--white-bg);
  transition: all .4s;
  display: flex;
  width: 26px;
  flex-shrink: 0;
  height: 26px;
  padding: 5px;
  line-height: 31px;
  text-align: center;
  align-items: center;
  border-radius: 100px;
  justify-content: center;
}
.accordion-content-v7 {
  display: flex;
  align-items: center;
  gap: 12px;
  padding-bottom: 12px;
}
.sofax-accordion-item-v7 {
  padding: 23px 24px;
  position: relative;
  margin-bottom: 24px;
  transition: all .4s;
}
.sofax-accordion-body-v7{
  padding: 10px 0 0 40px;
  display: none;
}
.sofax-accordion-item.contentv7.open {
  border: none;
  box-shadow: none!important;
}
.contentv7{
  border: none;
  box-shadow: none!important;
  padding: 0;
  p{
    padding-left: 40px;
  }
}