.sofax-slider-section {
    padding: 100px 0;
    @include max(991px) {
        padding: 80px 0;
    }
    @include max(767px) {
        padding: 60px 0;
    }
}
.sofax-title-section {
    text-align: center;
    margin-bottom: 60px;
    @include max(991px) {
        margin-bottom: 40px;
    }
}

.sofax-slider-logo{
    padding: 0 20px;
}
.sofax-logo-icon-item{
    padding: 0 20px;
}
.sofax-text-slider{
    &-wrap{
        display: flex!important;
        align-items: center;
        justify-content: center;
        white-space: nowrap;
        gap: 15px;
    }
    &-icon{
        flex-shrink: 0;
    }
}