
  /* end  sofax pricing section */

  .sofax-pricing{
    &-wrap{
        border-radius: 10px;
        padding: 30px;
        margin-bottom: 24px;
        transition: all 0.4s;
        background-color:  var(--white-bg);
        box-shadow: 0px 4px 80px 0px #00000014;
        &.active{
          box-shadow:  0px 4px 80px 0px #00000014;;
        }
    }
    &-header{
        margin-bottom: 15px;
        img{
            margin-bottom: 30px;
        }
    }
    &-price{
        gap: 10px;
        display: flex;
        align-items: flex-end;
        padding-bottom: 24px;
        margin-bottom: 24px;
        border-bottom: 1px solid var( --light-color);
        .sofax-price-value{
          font-weight: 700;
          color: var(--heading-color);
          font-size: 70px;
          line-height: 80px;
        }
    }
    &-body{
        margin-bottom: 40px;
        h5{
            font-weight: 600;
            margin-bottom: 20px;
        }
        ul{
            li{
                position: relative;
                font-weight: 600;
                padding-left: 40px;
                margin-bottom: 15px;
                &:last-child{
                    margin-bottom: 0;
                }
                img{
                    position: absolute;
                    left: 0;
                    top: 5px;
                }
            }
        }
    }
    &-bodyv4 {
        margin-bottom: 0!important;
    }
  }

//  sofax pricing section v4






.pricing-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 60px;
  @include max(991px) {
    margin-bottom: 40px;
  }
  @include max(767px) {
    margin-bottom: 30px;
  }
 label {
    margin: 0 20px;
    color: var(--heading-color);
    font-weight: 600;
    @include max(767px) {
      margin: 0 10px;
      font-size: 16px;
    }
  }
  &.dark{
    label{
      color: var(--heading-color);
    }
  }
}

[data-pricing-dynamic][data-value-active="yearly"] .dynamic-value:before {
  display: inline-block;
  content: attr(data-yearly);
  }
  [data-pricing-dynamic][data-value-active="yearly"] [data-pricing-trigger] {
  background: #ff5722;
  }
  
  [data-pricing-dynamic][data-value-active="yearly"] [data-pricing-trigger] span {
  left: calc(100% - 33px);
  }
  [data-pricing-dynamic][data-value-active="monthly"] .dynamic-value:before {
  display: inline-block;
  content: attr(data-monthly);
  }
  .dynamic-value:before {
    display: inline-block;
    content: attr(data-active);
  }
  .static-value:before {
    display: inline-block;
    content: attr(data-active);
  }
  .pricing-btn .btn-toggle {
    position: relative;
    width: 70px !important;
    height: 35px;
    border-radius: 50px;
    border: none;
    margin: 0;
    padding: 0!important;
    cursor: pointer;
    background-size: 25px;
    border: none!important;
    background-position: center right 5px;
    background-color: var(--accent-color);
    background-image: url(../images/v1/switcher-shape.svg)!important;
  }
  .pricing-btn .btn-toggle:focus {
    outline: none;
    box-shadow: none;
    background-image: url(../images/v1/switcher-shape.svg)!important;
  }
  .pricing-btn .btn-toggle:checked {
    background-position: center left 5px;
    background-image: url(../images/v1/switcher-shape.svg)!important;
  }


// v10

.pricing{
  &-wrapv10{
    border: 1px solid var(--light-color2);
    box-shadow: none;
  }
  &-headerv10{
    margin-bottom: 30px;
    h3{
      margin-bottom: 15px;
    }
  }
  &-pricingv10{
    background: var(--light-color);
    padding: 17px 54px 17px 58px;
    border-radius: 50px;
    margin-bottom: 0;
    justify-content: center;
  }
  &-bodyv10{
    margin-top: 30px;
    padding-top: 30px;
    border-top: 1px solid var(--light-color);
  }
}

