
header.sofax-header-section {
  position: fixed;
  z-index: 9;
  width: 100%;
  top: 0;
  padding:  0;
  transition: all 0.4s;
}
header.sofax-header-section.sticky-menu{
  box-shadow: 0 4px 80px rgb(0 0 0 / 6%);
  background-color: var(--white-bg);
}

header.sofax-header-section.sticky-menu.inner-header{

  background-color: var(--dark-bg)!important;
}
.inner-header .site-menu-main .nav-link-item {
  color: var(--white-color)!important;
  @include max(991px){
    color: var(--dark-bg)!important;
  }
}
// v9
.header-three-site-btn{
  align-items: center;
  gap: 12px;
  a.header-three-btn{
    color: var(--heading-color);
    font-size: 16px;
    line-height: 24px;
    font-weight: 700;
  }
}
