.sofax-section-padding2 {
    width: 100%;
}
.sofax-counter {
    &-wrap{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 30px;
        text-align: center;
        @include max (767px){
            justify-content: center;
        }
    }
    &-data {
        h2 {
            color: var(--white-bg);
        }
        p {
            color: var(--white-bg);
        }
    }
}
// sofax counter v5
.sofax-counter{
    &-wrap-v5 {
        display: flex;
        gap: 38px;
        justify-content: space-between;
        @include max (767px){
            display: block;
            text-align: center;
        }
        h2 {
            margin-bottom: 5px!important;
        }
    }
    &-data-v5 {
        h2 {
            @include max(991px){
                font-size: 60px!important;
                line-height: 65px;
                margin: 0;
            }
            @include max(767px){
                margin: 0!important;
            }
        }
        @include max (767px) {
            margin-bottom: 20px;
        }
        @include max (425px) {
            margin-bottom: 15px;
        }
    }
}
.sofax-countdown{
    &-wrap {
        display: flex;
        gap: 24px;
        justify-content: center;
        flex-wrap: wrap;
    }
    &-item{
        background-color: #FFFFFF;
        border-radius: 010px 010px 010px 010px;
        box-shadow: 0px 4px 80px 0px rgba(0, 0, 0, 0.08);
        padding: 035px 55px 035px 055px;
        text-align: center;
        font-size: 75px;
        line-height: 80px;
        font-weight: 700;
        color: var(--dark-bg);
        p{
            text-align: center;
            color: var(--accent-color);
            font-weight: 600;
            font-size: 21px;
            line-height: 30px;
        }
    }
}
.version-6{
    h2{
        font-size: 120px!important;
    }
}
.border-right{
    position: absolute;
    top: 15%;
    left: 30%;
    display: block;
    height: 80px;
    border-right: 1px solid var(--white-color);
    opacity: 10%;
    @include max (1199px){
        display: none;
    }
}
.border-right2{
    position: absolute;
    top: 15%;
    left: 70%;
    display: block;
    height: 80px;
    border-right: 1px solid var(--white-color);
    opacity: 10%;
    @include max (1199px){
        display: none;
    }
}
//  v7
.sofax-counter{
    &-wrapperv7 {
        display: flex;
        gap: 40px;
        @include max(991px){
            margin-top: 40px;
            justify-content: center;
        }
        @include max(767px){
            display: block;
        }
    }
    &-data-v7 {
        box-shadow: 0px 4px 80px 0px #00000014;
        margin-bottom: 24px;
        padding: 30px;
        border-radius: 5px;
        text-align: center;
        h2{
            margin-bottom: 5px!important;
        }
    }
}
// v8
.counter-version8{
    @include max (425px){
        display: block!important;
    }
}
.counterv8{
    
    h2{
        font-size: 50px!important;
        max-width: 430px;
        letter-spacing: 0!important;
        @include max (991px){
            max-width: 100%;
        }
        @include max (425px){
            margin-bottom: 15px;
        }
    }
}
