.portfolio-details{
    &-content{
        h2{
            margin-bottom: 40px;
        }
    }
    &-thumb{
        margin-bottom: 40px;
    }
    &-content2{
        margin-right: 30%;
        @include max (991px){
          margin-bottom: 15px;
        }
       p{
        font-size: 20px;
        margin-bottom: 5px;
       }
        a img{
            margin-left: 10px;
        }
    }
    &-content3 {
        h3{
            margin-bottom: 15px;
        }
        p{
            margin-bottom: 30px;
        }
    }
    &-content-bottom{
        margin-top: 40px;
        @include max (991px){
          margin-top: 25px;
        }
        h3{
            margin-bottom: 15px;
        }
        p{
            font-size: 20px;
        }
    }
}

.sofax-portfolio-content-wrap{
  padding: 0 15px;
  &:hover .sofax-portfolio-thumb img{
    transform: scale(1.1);
  }
  .sofax-portfolio-thumb{
    border-style: solid;
    border-width: 3px 3px 3px 3px;
    border-color: #0E0E0E;
    border-radius: 16px;
    overflow: hidden;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: all .8s;
    }
  }
}

.sofax-portfolio-authore{
  padding-bottom: 40px;
  border-bottom: 1px solid var(--light-color);
  margin-bottom: 40px;
  @include max(991px){
    padding-bottom: 25px;
    padding-bottom: 25px;
  }
  &-wrap{
      max-width: 700px;
      @include max (991px){
          max-width: 100%;
      }
  }
  &-text{
      margin-bottom: 24px;
      &:last-child{
          margin-bottom: 0;
      }
      h4{
        margin-bottom: 10px;
      }
  }
}
.sofax-portfolio-author {
  &-wrap {
      display: flex;
      justify-content: space-between;
      margin-bottom: 30px;
  }
  &-data{
      h4 {
          margin-bottom: 5px;
      }
      p{
        color: var(--body-color);
      }
  }
  &-icon{
      align-self: center;
      @include max (991px){
          img {
              width: 80%;
          }
      }
      @include max (767px){
          img {
              width: 70%;
          }
      }
  }
}
.sofax-portfolio-menu{
  ul{
      li{
          display: inline-block;
          border: 1px solid var(--dark-bg);
          border-radius: 50px;
          padding: 15px 40px;
          cursor: pointer;
          margin-left: 24px;
          font-size: 16px;
          font-weight: 700;
          color: var(--dark-bg);
          @include max(991px){
            margin-bottom: 24px;
          }
          @include max(425px){
            margin-left: 15px;
            padding: 13px 30px;
            &:nth-child(3){
              margin-bottom: 0;
            }
          }
          
          &:last-child{
            margin-bottom: 0;
          }
          @include max(375px){
           
            padding: 15px 55px;
            &:nth-child(2){
              margin-left: 15px;
            }
            &:nth-child(3){
              margin-bottom: 24px;
            }
          }
          &.active{
              background: var(--accent-color);
              border: 1px solid var(--accent-color);
          }
      }
  }
}
.sofax-portfolio-wrap{
  margin: 0 24px 24px 24px;
}



.wrapper {
  width: 1000px;
  margin: auto;
  display: block;
}

.btn {
  text-transform: uppercase;
  font-size: 12px;
  padding: 5px 15px;
  background: #d6d6d6;
  display: inline-block;
  margin-bottom: 10px;
  text-decoration: none;
  color: #fff;
}
.btn:hover {
  background: #aaa;
}

/* ---- button ---- */

.button {
  display: inline-block;
  padding: 10px 18px;
  margin-bottom: 10px;
  background: #EEE;
  border: none;
  border-radius: 7px;
  background: #f3f3f3;
  color: #222;
  font-family: sans-serif;
  font-size: 16px;
  text-shadow: 0;
  cursor: pointer;
}

.button:hover {
  background-color: #8CF;
  color: #222;
}

.button:active,
.button.is-checked {
  background-color: #aa96ca;
}

.button.is-checked {
  color: white;
}


/* ---- button-group ---- */

.button-group:after {
  content: '';
  display: block;
  clear: both;
}

.button-group .button {
  float: left;
  border-radius: 0;
  margin-left: 0;
  margin-right: 1px;
  font-family: arial;
  text-transform: uppercase;
  font-size: 12px;
  letter-spacing: 1px;
}


/* ---- isotope ---- */

.grid {
  border: 0;
}

/* clear fix */
.grid:after {
  content: '';
  display: block;
  clear: both;
}

/* ---- .element-item ---- */

.element-item {
  position: relative;
  float: left;
  width: 300px;
  height: 300px;
  margin: 5px;
  padding: 0;
  background: #888;
  color: #262524;
  overflow: hidden;
}
.element-item img {
  max-width: 100%;
  height: auto;
}

.element-item > * {
  margin: 0;
  padding: 0;
}

.element-item .name {
  position: absolute;

  left: 10px;
  top: 60px;
  text-transform: none;
  letter-spacing: 0;
  font-size: 12px;
  font-weight: normal;
}

.element-item .city {
  position: absolute;
  left: 10px;
  top: 0px;
  font-size: 42px;
  font-weight: bold;
  color: white;
}

.element-item .date {
  position: absolute;
  right: 8px;
  top: 5px;
}

.element-item.city1          { background: #F00; background: hsl(   0, 100%, 50%); }
.element-item.city2  { background: #F80; background: hsl(  36, 100%, 50%); }
.element-item.city3      { background: #FF0; background: hsl(  72, 100%, 50%); }