
.sofax-popup-video {
    color: var(--white-bg);
    background-color: #0E0E0E;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    padding: 18px 30px;
    border-radius: 50px;
    display: inline-block;
    &:hover {
        color: var(--white-bg);
    }
    img {
        margin-right: 10px;
        margin-bottom: 2px;
    }
}


// sofax about use
//  sofax hero5 video
.sofax-aboutus-thumb .sofax-popup-video3{
    top: 35%;
    left: 45%;
    text-align: center;
    position: absolute;
    transform: translate(-50%, -50%);
    animation: animName 10s linear infinite;
    @include max(1199px){
        top: 36%;
        left: 42%;
    }
    @include max(991px){
        top: 35%;
        left: 40%;
    }
    @include max(425px){
        left: 30%;
    }
    @include max(767px){
        text-align: center;
        top: 35%;
        left: 35%;
    }
    img {
        background: var(--white-color);
        border: 2px solid var(--dark-bg);
        border-radius: 100px;
        padding: 8px;
        box-shadow: none !important;
        @include max(1199px){
            width: 70%;
        }
        @include max(991px){
            width: 60%;
        }
        @include max(767px){
            width: 45%;
        }
        @include max(525px){
            width: 30%;
        }
    }
}
.sofax-aboutus-thumb .play-btn2{
    img{
        box-shadow: none!important;
        position: absolute;
        top: 48%;
        left: 51%;
        transform: translate(-50%, -50%);
        @include max (1199px){
            left: 50%;
            top: 49%;
            width: 20px;
        }
        @include max (991px){
            left: 51%;
            width: 15px;
        }
        @include max (767px){
            left: 50%;
            width: 12px;
        }
    }
}
// v9
.videov9{
    img{
        width: 25px;
    }
}