.sofax-iconbox{
    &-wrap {
        border-radius: 10px;
        background-color: var(--white-bg);
        padding: 30px;
        margin-bottom: 24px;
        transition: all 0.4s;
        &:hover{
            box-shadow: 0px 4px 80px 0px #00000014;
        }
    }
    &-icon {
        margin-bottom: 30px;
        &.content-v5 {
            margin-bottom: 0;
        }
    }
    &-data {
        h4 {
            margin-bottom: 15px;
        }
        p {
            font-size: 20px;
        }
        &.content-v5 {
            h4 {
                margin-bottom: 0;
            }
        }
    }
    // end icon box one v1
    &-wrap2 {
        display: flex;
        gap: 12px;
        margin-bottom: 24px;
        text-align: left;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &-icon2 {
        flex-shrink: 0;
    }
    &-data2 {
        h5 {
            margin-bottom: 5px;
        }
        h4 {
            margin-bottom: 15px;
        }
        a {
            color: var(--white-color);
            font-size: 20px;
            &:last-child{
                margin-bottom: 0;
            }
        }
    }
}
// sofax v6

.sofax-list-icon-iconv6{
    ul{
        li{
            font-size: 23px;
            font-weight: 700;
            line-height: 32px;
            text-align: left;
            color: #0e0e0e;
            margin-bottom: 16px;
            img{
                margin-right: 15px;
            }
        }
    }
}
