//  hero5 thumb
.hero5-thumb {
    position: relative;
    padding: 0 50px;
    img {
        box-shadow: 8px 8px 0px 0px var(--dark-bg);
        border-radius: 8px;
        border: 1px solid var(--dark-bg);
        @include max (991px){
            box-shadow: 5px 5px 0px 0px var(--dark-bg);
        }
    }
    .sofax-popup-video2{
        top: 42%;
        left: 45%;
        text-align: center;
        position: absolute;
        transform: translate(-50%, -50%);
        animation: animName 10s linear infinite;
        @include max(1199px){
            top: 40%;
            left: 42%;
        }
        @include max(991px){
            top: 40%;
            left: 40%;
        }
        @include max(425px){
            left: 30%;
        }
        @include max(767px){
            text-align: center;
        }
        img {
            box-shadow: none !important;
            border: 2px solid var(--dark-bg);
            border-radius: 100px;
            background: var(--white-color);
            padding: 8px;
            @include max(1199px){
                width: 70%;
            }
            @include max(991px){
                width: 60%;
            }
            @include max(767px){
                width: 50%;
            }
            @include max(525px){
                width: 30%;
            }
        }
    }
    .play-btn{
        img{
            position: absolute;
            top: 52%;
            left: 49%;
            transform: translate(-50%, -50%);
            box-shadow: none!important;
            border: none;
            @include max (1199px){
                left: 49.5%;
                top: 53%;
                width: 20px;
            }
            @include max (991px){
                left: 50%;
                width: 15px;
                top: 56%;
            }
            @include max (767px){
                width: 12px;
                top: 53%;
            }
        }
    }
}
