
.sofax-blog-wrap-v4{
    &:hover .sofax-blog-img img{
        transform: scale(1.1);
    }
}
.sofax-blog {
    &-wrap-v4 {
        box-shadow: 0px 4px 80px 0px #00000014;
        border-radius: 10px;
        background: #F9F9FA;
        margin-bottom: 24px;
        padding: 20px;
        cursor: pointer;
        &:hover {
            background-color: var(--white-bg);
        }
    }
    &-meta{
        display: flex;
        align-items: center;
        gap: 15px;
        padding: 24px 0 15px 0;
        ul {
            li{
                color: var(--body-color);
            }
        }
        img {
            @include max (991px){
                width: 100%;
            }
        }
       
    }
    &-content h4 {
        margin-bottom: 30px;
        font-size: 22px!important;
    }
    &-img {
        overflow: hidden;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all .8s;
        }
        @include max (991px){
            text-align: center;
        }
    }
}


.sofax-inner-blog-wrap{
    &:hover .sofax-inner-blog-img img {
        transform: scale(1.1);
    }
    .sofax-inner-blog-img{
        overflow: hidden;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all .8s;
        }
    }
}
.sofax-inner-blog{
    &-wrap{
        border: 1px solid var(--dark-bg);
        padding: 30px;
        border-radius: 20px;
        margin-bottom: 30px;
    }
    &-img{
        img{
            width: 100%;
        }
    }
    &-meta{
        a {display: flex;
            align-items: center;
            gap: 15px;
            padding: 30px 0 24px 0;
            h5{
                background: var(--dark-bg);
                color: var(--white-color);
                padding: 12px 15px;
                border-radius: 5px;
            }
            ul {
                li{
                    font-size: 20px;
                    line-height: 28px;
                    color: var(--body-color);
                }
            }
        }
        
        img {
            @include max (991px){
                width: 100%;
            }
        }
    }
    &-text{
        h3{
            margin-bottom: 15px;
            @include max (767px){
                font-size: 28px!important;
            }
        }
        p{
            font-size: 19px!important;
            margin-bottom: 24px;
        }
    }
}


// sofax blog details inner page
.sofax-inner-blog-details{
    &-wrap{}
    &-img{
        margin-bottom: 40px;
        img{
            width: 100%;
        }
    }
    &-content{
        margin-bottom: 30px;
        h3{
            margin-bottom: 15px;
        }
    }
    &-content-data{
        margin-bottom: 24px;
        &:last-child{
            padding-bottom: 40px;
            border-bottom: 1px solid var(--light-color);
            margin-bottom: 40px;
        }
        h4{
            margin-bottom: 10px;
        }
        p{
            font-size: 20px;
        }
    }
    &-content2{
        background: var(--dark-bg);
        padding: 30px;
        border-radius: 10px;
        margin-bottom: 30px;
    }
}
.sofax-blog-tag{
    &-wrapper {
        display: flex;
        justify-content: space-between;
        padding-bottom: 40px;
        border-bottom: 1px solid var(--light-color);
        margin-bottom: 40px;
        flex-wrap: wrap;
    }
    &-wrap{
        
        h4{
            margin-bottom: 24px;
        }
        ul{
            li{
                display: inline-block;
                margin: 0 5px 5px 0px;
            }
        }
    }
}
.sofax-post-navigation-wrap{
    display: flex;
    gap: 24px;
    max-width: 300px;
    .title-post-content{
        ul{
            li{
                font-size: 15px!important;
                line-height: 16px;
                font-weight: 400;
                margin-bottom: 10px;
            }
        }
    }
}
.nav-preview{
    &-wrap{
        @include max(991px){
            margin-bottom: 24px;
            &:last-child{
                margin-bottom: 0;
            }
        }
        
    }
    &-icon{
        margin-bottom: 24px;
        a{
            color: var(--dark-bg);
            font-size: 13px;
            font-weight: 700;
            display: flex;
            align-items: center;
            gap: 10px;
        }
    }
}
.sofax-post-navigation-wrapper{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-bottom: 40px;
    border-bottom: 1px solid var(--light-color);
    margin-bottom: 40px;
}
.sofax-post-comment{
    h3{
        margin-bottom: 30px;
    }
    ul{
        li{
            margin-bottom: 30px;
            &.children{
                padding-left: 80px;
                @include max (767px){
                    padding-left: 0;
                }
            }
            .children2{
                border-top: 1px solid var(--light-color);
                padding-top: 30px;
                border-bottom: 1px solid var(--light-color);
                padding-bottom: 30px;
            }
        }
    }
    &-wrap{
        display: flex;
        gap: 20px;
        &:last-child{
            margin-bottom: 40px;
        }
    }
    &-data{
        strong{
            display: block;
            font-weight: 700;
            font-style: 16px;
            margin-bottom: 5px;
            color: var(--dark-bg);
        }
        span{
            display: block;
            font-weight: 400;
            font-size: 16px;
            margin-bottom: 10px;
        }
        p{
            font-size: 18px;
            line-height: 28px;
        }
        &-wrap {
            display: flex;
            justify-content: space-between;
            align-items: center;
            ul {
                li{
                    
                    a{
                        font-weight: 700;
                        font-size: 16px;
                        color: var(--dark-bg);
                        transition: all .4s;
                        &:hover{
                            color: var(--accent-color);
                        }
                    }
                }
            }
        }
    }
}
.ml-650{
    margin-left: 65%;
    @include max(767px){
        margin-left: 0;
    }
}
.mt-30{
    margin-top: 30px;
    @include max(767px){
        margin-top: 20px;
    }
}
// ver7
.blog-v7{
    h4{
        margin-bottom: 10px!important;
    }
    p{
        color: var(--body-color);
        font-size: 20px!important;
        margin-bottom: 24px;
    }
}
.blog-versionv7{
    svg{
        margin-left: 5px;
        path{
            transition: all 0.4s;
        }
    }
    &:hover{
        color: var(--accent-color)!important;
        svg path{
            stroke: var(--accent-color);
        }
    }
}
// vr9
.sofax-blog-wrap-v9{
    border: 1px solid var(--light-color2);
    padding: 30px;
    .blog-btn-icon-wrap {
        display: flex;
        align-items: center;
        gap: 10px;
    }
    .sofax-blog-metav9 {
        display: flex;
        gap: 24px;
        padding: 24px 0 24px 0;
    }
    .market-contentv9{
        p{
            margin-bottom: 0!important;
        }
        ul{
            li{
                color: var(--body-color);
            }
        }
    }
}



