.sofax-features {
  &-box {
    border: 1px solid var(--light-color);
    border-radius: 10px;
    padding: 27px;
    margin-bottom: 24px;
    transition: all .4s;
  }
  &-icon {
    margin-bottom: 30px;
  }
  &-content {
    h4{
      margin-bottom: 15px;
    }
    p {
      font-size: 20px;
    }
  }

}
.features-v2 {
  background-color: var(--accent-color);
  padding: 30px 30px 0 30px;
}
.features-v5{
  border: 1px solid var(--dark-bg)!important;
  padding: 22px;
  &:hover{
    box-shadow: 5px 5px 0 0 #0e0e0e;
  }
}
.sofax-features{
  &-boxv6{
    padding: 30px;
    background: var(--white-color);
    border-radius: 5px
  }
  &-imgv6{
    img{
      box-shadow: 0px 4px 80px 0px #00000014;
      border-radius: 10px;

    }
  }
  &-author-data{
    h4{
      padding: 30px 0 15px 0;
    }
    p{
      color: var(--body-color);
    }
  }
}
// index-06
.sofax-features-boxv6{
  &:hover .sofax-features-imgv6 img{
    transform: scale(1.1);
  }
  .sofax-features-imgv6{
    box-shadow: 0px 4px 80px 0px #00000014;
    border-radius: 10px;
    overflow: hidden;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: all .8s;
    }
  }
}
//  07
.sofax-features{
  &-boxv7{
    border: 1px solid var(--light-color2);
    border-radius: 5px;
    padding: 30px;
    margin-bottom: 24px;
  }
  &-iconv7{
    margin-bottom: 24px;
  }
  &-contentv7{
    h4{
      margin-bottom: 12px;
    }
    p{
      margin-bottom: 0!important;
    }
  }
}
// vr8

.features-vr8{
  padding: 24px 24px 24px 24px;
  box-shadow: 0px 4px 80px 0px #00000014;
  border-radius: 5px;
  background: var(--white-color);
  position: relative!important;
  z-index: 1;
  @include max (991px){
    margin-bottom: 24px!important;
  }
  h4{
    margin-bottom: 10px!important;
  }
}
.sofax-features{
  &-wrapv8{
    display: flex;
    @include max (991px){
      display: block;
    }
  }
  &-boxv8{
    border: 1px solid var(--light-color);
    padding: 30px;
    @include max (991px){
      margin-bottom: 24px;
    }
  }
  
}

