.sofax-testimonial-slider-nav  {
  position: absolute;
  left: -30px;
  top: 100px;
  @include max(991px) {
    top: 20%;
  }
  @include max(767px) {
    left: 0;
    top: 5%;
  }
  .slick-list{
    overflow: inherit;
  }
  .slick-slide{
    width: 57px!important;
    height: 57px;
    float: inherit;
    margin-bottom: 15px;
    @include max (991px){
      margin-bottom: 30px;
    }
  }
  .slider-sync-item{
    cursor: pointer;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: all 0.4s;
      border: 2px solid transparent;
    }
  }
  .slick-slide.slick-active.is-active{
    .slider-sync-item{
        img {
          border: 2px solid var(--accent-color);
          border-radius: 100px;
        }
    }
  }
}
.br-50 img{
  border-radius: 100px;
}

