// sofax testimonal slider content v5
.sofax-default {
  &-contentv5{
    p {
      max-width: 630px;
      @include max (1199px){
        max-width:100%;
      }
    }
  }
}
.sofax-testimonial-slider{
  &-wrapv5{
    padding: 30px;
    border: 1px solid var(--dark-bg);
    border-radius: 5px;
    box-shadow: -7px -7px 0px -3px var(--dark-bg) inset;
    margin: 0 10px 10px 0;
