    }
    @include max (1199px){
      margin-top: 30px;
    }
  }
  &-ratingv5{
    margin-bottom: 15px;
    ul{
      li{
        display: inline-block;
      }
    }
  }
  &-datav5{
    margin-bottom: 24px;
  }
  &-authore-wrap {
    display: flex;
    gap: 10px;
  }
}

.sofax-testimonial-2column-sliderv5 .sofax-t-next:hover,
.sofax-testimonial-2column-sliderv5 .sofax-t-prev svg {
  background-color: var(--accent-color);
  border: 1px solid var(--accent-color);
}
.sofax-testimonial-2column-sliderv5 .sofax-t-prev:hover {
  background-color: var(--accent-color);
  border: 1px solid var(--accent-color);
}
.sofax-testimonial-2column-sliderv5 .sofax-t-next, 
.sofax-testimonial-2column-sliderv5 .sofax-t-prev {
  left: 0;
  bottom: -27%;
  width: 50px;
  height: 50px;
  border-radius: 50px;
  z-index: 99;
  cursor: pointer;
  position: absolute;
  background-size: 30px;
  background-position: center;
  background-repeat: no-repeat;
  transition: all .4s;
  background-image: url(../images/v5/left-arrow.svg);
  border: 1px solid var(--dark-bg);
  @include max(1399px) {
    left: 0;
    bottom: -20%;
  }
  @include max(1199px) {
    bottom: -20%;
  }
  @include max(767px) {
    display: none;
  }
}
.sofax-testimonial-2column-sliderv5 .sofax-t-prev {
  left: 13%;
  bottom: -27%;
  background-image: url(../images/v5/right-arrow.svg);
  @include max (1399px){
    left: 15%;
    bottom: -20%;
  }
  @include max (1199px){
    left: 7%;
    bottom: -20%;
  }
  @include max(991px) {
    left: 10%;
  }
  @include max(767px) {
    display: none;
  }
}
.tac2{
  @include max (1199px){
      h2,p{
          text-align: center;
      }
  }
}
.tac3{
  @include max (991px){
    text-align: center;
  }
}
// v7
.sofax-testimonial{
  &-wrapperv7{
    position: relative;
    z-index: 0;
    padding: 30px;
    &::before{
      content: "";
      background-color: var(--white-bg);
      border-radius: 5px;
      width: 100%;
      height: 77%;
      position: absolute;
      z-index: -1;
      bottom: 10px;
      left: 0;
    }
  }
  &-wrapv7{
    display: flex;
    gap: 18px;
  }
  &-thumbv7{
    width: 42%;
    padding-bottom: 20px;
    @include max (991px){
      width: 30%;
    }
  }
  &-rattingv7 {
    margin-top: 50px;
    @include max (991px){
      margin-top: 90px;
    }
    @include max (767px){
      margin-top: 50px;
    }
    ul{
      li{
        display: inline-block;
        margin-bottom: 10px;
      }
    }
  }
  &-authorev7{
    h5{
      margin-bottom: 5px;
    }
  }
}
// v8
.sofax-testimonial-thumbv8 {
  position: relative;
  display: block;
  margin-bottom: 24px;
  text-align: center;
}
.sofax-testimonial-thumbv8::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(14, 14, 14, 0.8);
  opacity: 0;
  transition: .3s;
}
.sofax-testimonial-thumbv8:hover::before {
  opacity: 1;
}
.sofax-testimonial-btnv8 {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  opacity: 0;
  transition: all .3s;
  text-align: center;
  p{
    font-size: 16px!important;
  }
  .right-icon-btn{
    width: 24px;
    height: 24px;
    border-radius: 100px;
    background: var(--accent-color);
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 70%;
    left: 40%;
  }
}
.sofax-testimonial-itemv8:hover .sofax-testimonial-thumbv8::before{
  opacity: 1;
}
.sofax-testimonial-itemv8:hover .sofax-testimonial-btnv8 {
  opacity: 1;
}

.testimonial-vr8{
  padding-bottom: 24px;
  border-bottom: 1px solid var(--light-color2);
  margin-bottom: 24px;
}
.sofax-testimonial-authore{
  &-wrapv8{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &-data{
    h5{
      margin-bottom: 5px;
    }
  }
  &-icon{
    ul{
      li{
        display: inline-block;
      }
    }
  }
}