.sofax-hero {
    &-shape {
        position: absolute;
        left: 0;
        top: -27%;
        @include max (1399px) {
            left: 0;
            top: -26%;
            width: 20%;
        }
        @include max (1199px) {
            left: -9%;
            top: -27%;
            width: 20%;
        }
        @include max(991px){
            display: none;
        }
        img {
            width: 100%;
            height: auto;
        }
    }
   
    // end sofax hero shape one v1
    &-shape2 {
        position: absolute;
        right: -14%;
        bottom: 68%;
        img {
            width: 100%;
            height: auto;
        }
        @include max (1399px) {
            right: -15%;
            bottom: 63%;
        }
        @include max (1199px) {
            right: -11%;
            top: 6%;
            width: 20%;
        }
        @include max (991px) {
            display: none;
        }
    }
}
// end hero shape v1
.sofax-card-shape {
    position: absolute;
    bottom: 20px;
    right: -115px;
    overflow: hidden;
    transform: translatey(0px);
    -webkit-animation: updown 3s ease-in-out infinite;
    animation: updown 3s ease-in-out infinite;
    @include max(1399px) {
        bottom: 50px;
        right: -115px;
        img{
            width: 80%;
        }
    }
    @include max(1199px) {
        display: none;
        img{
            width: 100%;
        }
    }
}
.sofax-content-shape-v1{
    position: absolute;
    top: -14%;
    right: -16%;
    @include max(1399px) {
        display: none;
    }
}
